

body {
  min-width: 320px;
  position: relative;
  overflow-x: hidden;
  transition: opacity 0s 400ms;
  background-color: $color-background;
  height: auto; }


article {
  box-sizing: border-box; }

a {
	background-color: transparent;
	-webkit-text-decoration-skip: objects;
	text-decoration: none;
	color: inherit; }

.br-xs {
	display: block;
	@include minsm {
		display: none; } }

.br-sm {
	display: none;
	@include minsm {
		display: block; } }

/**************** HEADER AREA ****************/

.page {
	&__header {
		border-bottom: 1px solid $color-border;
		@include minsm {
			border:none {} } } }



.top {
	@include minsm {
		border-bottom: 1px solid $color-border; } }

.topline {
	display: none;
	@include minsm {
		@include flexbox;
		@include justify-content(space-between);
		@include align-items(center);
		min-height: 80px; } }

.phones {
	margin: 0;
	&__item {
		font-size: 16px;
		line-height: 1;
		&:first-of-type {
			margin: 0 auto 10px; }
		&:last-of-type {
			margin: 10px auto -0px; } } }

.header__inner {
	padding-top: 10px;
	.text-accent {
		text-transform: uppercase; }
	.hero__text {
		margin: 25px auto;
		max-width: 485px;
		@include minsm {
			margin: 30px auto 20px; } }
	.timer {
		margin: 50px auto;
		@include minsm {
			margin: 12px auto 22px; } }
	@include minsm {
		padding-bottom: 50px; } }

.hero {
	text-align: center; }


.maintitle {
	max-width: 400px;
	margin: 0 auto;
	@include minsm {
		max-width: 100%;
		padding-bottom: 0; }
	&__text {
		margin: 0; }
	&__text_accent {
		margin: 0 auto 25px;
		font-size: 16px;
		@include pseudo;
		@include minsm {
			max-width: 660px;
			margin-bottom: 10px;
			&:before, &:after {
				@include center(vertical);
				width: 53px;
				height: 1px;
				background-color: $color-accent-1; }
			&:before {
				left: -38px;
				right: auto; }
			&:after {
				right: -38px;
				left: auto; } } } }


.hero__title {
	padding: 23px 20px 28px;
	border: 1px solid $color-border-accent;
	@include borad(5px,all);
	@include minsm {
		border: none; } }





/**************** SECTIONS ****************/

.section {
	position: relative;
	padding-top: 1.5rem;
	padding-bottom: 1.5rem;
	@include minsm {
		padding-bottom: 2.5rem;
		padding-top: 2.5rem; }
	&__block {
		@include flex-wrap(wrap); }
	&__title {
		text-align: center;
		margin: 0em auto 1em; } }

.img {
	text-align: center; }
.img-wrapper {
	text-align: center;
	display: inline-block;
	@include borad(7px,all);
	img {
		@include borad(7px,all); }
	&__heading {
		font-weight: bold;
		margin: 17px auto;
		line-height: 1; }
	&__text {
		font-size: 14px;
		max-width: 190px;
		margin: 20px auto;
		line-height: 1.25;
		&_bold {
			font-size: 18px;
			font-weight: bold; } }
	&_figure {
		border: 1px solid $color-border;
		img {
			@include borad(7px,top-left);
			@include borad(7px,top-right);
			@include borad(0px,bottom-left);
			@include borad(0px,bottom-right); } } }

.text__item {
	margin: 30px auto;
	&:last-of-type {
		margin-bottom: 0; }
	&:first-of-type {
		margin-top: 0; } }


.decorated_cloud {
	position: relative;
	margin: 1rem auto;
	text-align: center;
	max-width: 345px;
	&_1 {

		margin: 1rem auto;
		height: 175px;
		background: url(../img/circle.png) no-repeat center center;
		background-size: cover;
		text-align: center;
		@include minsm {
			max-width: 675px;
			height: 90px;
			background: url(../img/circle2.png) no-repeat center center;
			background-size: cover;
			margin: 2.5rem auto; } }
	&_2 {

		height: 135px;
		background: url(../img/circle5.png) no-repeat center center;
		background-size: cover;
		@include minsm {
			max-width: 470px;
			height: 134px;
			background: url(../img/circle4.png) no-repeat center center;
			background-size: cover; } }
	&_3 {
		height: 135px;
		background: url(../img/circle5.png) no-repeat center center;
		background-size: cover;
		@include minsm {
			max-width: 400px;
			height: 103px;
			background: url(../img/circle6.png) no-repeat center center;
			background-size: cover;
			margin: 2rem auto 1.5rem; } }
	&_4 {
		max-width: 259px;
		height: 185px;
		background: url(../img/circle7.png) no-repeat center center;
		background-size: cover;
		@include minsm;

		.decorated__text {
			padding: 0 25px; } } }


.decorated__text {
	@include center(vertical);
	padding: 0 15px;
	&.p_modified {}
	@include center(both);
	width: 100%; }





.question {
		padding-bottom: 2.5rem; }


// LIST

.list {
	padding-left: 50px;
	&__item {
		@include pseudo;
		&:not(:last-of-type) {
			margin-bottom: 30px; }
		&:before {
			width: 28px;
			height: 28px;
			left: -50px; }
		&_negative {
			&:before {
				@include image("../icons/cross.png");
				@include cover; } }
		&_positive {
			&:before {
				@include image("../icons/check.png");
				@include cover; } } } }

