.date {
	background-color:#fff {}
	position: relative;
	padding-bottom: 2.5rem;
	z-index: 1;
	.section__inner {
		box-sizing: border-box;
		background-color:#fff {}
		margin-bottom: -85px;
		padding: 2rem 0px;
		border: 2px solid $color-accent-1;
		@include borad(5px,all);
		text-align: center;
		@include minsm {
			padding-bottom: 80px; } }
	&__footer {
