		margin: 2rem auto 0px;
		&:after {
			display: none;
			@include minsm {
				display: block;
				margin: 25px auto 0; } } } }

.texting {
	&__block {
		position: relative;
		&_fluid {
			max-width: 285px;
			height: 69px;
			margin: 1.5rem auto;
			@include image("../img/cloud1-xs.png");
			@include cover;
			.text {
				@include center(vertical);
				&__item {
					height: 69px;
					padding: 0 30px 0px 70px; } }
			@include minsm {
				max-width: 550px;
				height: 46px;
				margin: 50px auto;
				@include image("../img/cloud1-sm.png");
				@include cover;
				@include transform(translate(-44px,0));
				.text {
					@include center(vertical);
					&__item {
						height: 46px;
						padding: 4px 30px 0px 150px; } } } }
		&_big {
			max-width: 238px;
			height: 386px;
			margin: 1.5rem auto;
			@include image("../img/cloud2-xs.png");
			@include cover;
			padding: 30px -0px 20px;
			&:after {
				position: absolute;
				content: " ";
				display: block;
				bottom: -10px;
				right: -45px;
				@include image("../img/zimb_small.png");
				background-position: 	50% 50%;
				background-repeat: 	no-repeat;
				width: 37px;
				height: 47px; }
			.text {
				position: absolute;
				top: 30px; }
			@include minsm {
				max-width: 510px;
				height: 383px;
				margin: 1.5rem auto;
				@include image("../img/cloud2-sm.png");
				@include cover;
				padding: 30px -0px 20px;

				.text {
					top: 35px; } } }

		&_small {
			max-width: 232px;
			height: 245px;
			margin: 1.5rem auto;
			@include image("../img/cloud3-xs.png");
			@include cover;
			padding: 30px -0px 20px;
			&:after {
				position: absolute;
				content: " ";
				display: block;
				bottom: -10px;
				right: -45px;
				@include image("../img/zimb_small.png");
				background-position: 	50% 50%;
				background-repeat: 	no-repeat;
				width: 37px;
				height: 47px; }
			.text {
				position: absolute;
				top: 30px;
				&__item {
					font-size: 22px; } }
			@include minsm {
				max-width: 510px;
				height: 185px;
				margin: 1.5rem auto;
				@include image("../img/cloud3-sm.png");
				@include cover;
				padding: 30px -0px 20px;
				.text {
					top: 35px; }
				.text_big {
					margin: 25px auto !important; } } } }
	.text__item {
		margin: 1rem auto;
		&:last-of-type {
			margin-bottom: 0; }
		&:first-of-type {
			margin-top: 0; } }
	.text_big {
		display: inline-block;
		margin: 5px auto;
		font-size: 35px;
		font-weight: bold;
		font-family: "Akrobat", sans-serif; } }



/**************** FOOTER AREA *********************/



.page__footer {
	background-color: $color-footer-background;
	color: $white;
	padding: 25px 0 32px;
	@include rmin(768) {
		padding: 50px 0 45px; }
	.footer-wrapper {
		@include flex-wrap(wrap);
		@include justify-content(center);
		max-width: 322px;
		margin: 0 auto;
		@include rmin(768) {
			max-width: 960px;
			@include justify-content(space-between);
			@include align-items(flex-start); } }

	.footer-block-1 {
		@include flex(0 0 300px); }

	.inner-wrapper {
		max-width: 248px;
		margin-left: 0;
		margin-right: auto; }

	.footer-logo-wrapper {
		max-width: 165px;
		margin-right: 0;
		margin-left: auto;
		@include rmin(768) {
			max-width: 250px; } }

	.support-list {
		margin: 10px auto 0;
		list-style: none;
		padding-left: 0;
		@include rmin(768) {
			margin-top: 20px; }
		.support-text {
			font-size: 18px;
			margin: 5px auto;
			img {
				margin-right: 7px; } } }

	.footer-block-2 {
		margin-top: 58px;
		text-align: center;
		@include rmin(768) {
			text-align: left;
			margin-top: 0; } }

	.social-title {
		margin: 0; }

	.social-links {
		margin: 5px auto 10px;
		max-width: 244px;
		@include justify-content(space-between);
		@include rmin(768) {
			margin: 10px auto 10px 0; }
		.social-link {
			@include flex(0 0 52px); } }


	.social-copyrights {
		margin: 10px auto;
		font-size: 15px;
		@include rmin(768) {
			margin: 6px auto; } }

	.link-references {
		color: $color-text-weak;
		text-decoration: underline;
		font-size: 15px;
		line-height: 1.1; } }


