
html {
  @include font-set ($font-name:PT Sans,$size: 18px, $colour: $color-text, $weight: 400,  $lh: false); }


h1,h2,h3,h4,h5,h6 {
	@include font-set($font-name:"Akrobat",$size: false, $colour:  false, $weight: 900,  $lh: 1.2); }


@include font ("PT Sans", "PTSans/PTSansBold/" , pt-sans-v8-latin_cyrillic-700);
@include font ("PT Sans", "PTSans/PTSansRegular/" , pt-sans-v8-latin_cyrillic-regular);
@include font ("Akrobat", "Akrobat/AkrobatBold/" , Akrobat-ExtraBold);


.dark-bg {
  color: $color-text-invert; }

.darken-bg {
	background-color: $color-background-darken; }

.text-center {
	text-align: center; }
.text-accent-1 {
  color: $color-accent-1; }

.text-accent-2 {
	color: $color-text-invert;
	padding: 0px 10px; }

.text-accent-2-1 {
	background: url(../img/accent1_red.png) no-repeat center center;
	background-size: cover; }

.text-accent-2-2 {
	background: url(../img/accent1_red.png) no-repeat center center;
	background-size: cover; }

.text_urgent {
	font-size: 22px;
	font-weight: bold;
	text-align: center; }

.h1 {
	font-size: 40px;
	@include minsm {
		font-size: 42px; } }
.subheading, .h3 {
	font-size: 22px; }


.h2 {
	font-size: 40px;
	@include minsm {
		font-size: 42px; } }

.text_separated_big {
	font-size: 35px;
	margin-bottom: 0; }


