* {
	box-sizing: border-box; }

.flex {
  @include flexbox();
 	@include justify-content(center);
 	@include minsm {
 		@include justify-content(space-between); } }
.container {
	width: 90%;
	max-width: 610px;
	margin: 0 auto;
	@include rmin(768) {
		width: 90%;
		max-width: 803px; }
	&_small {
		@include rmin(768) {
			max-width: 600px;
			margin: 0 auto; } } }


// DEBUGGER-flexbox
.IE-flex-debugger {
	padding: 0;
	border: 0; }


// GRID
.grid {
	&-col-10 {
			@include flex(0 0 100%); }
	&-col-4 {
		@include flex(0 0 100%);
		@include minsm {
			@include flex(0 0 40%); }
		&.right {
			@include minsm {
				padding-left: 35px; } } }
	&-col-6 {
		@include flex(0 0 100%);
		@include minsm {
			@include flex(0 0 60%); }
		&.right {
			@include minsm {
				padding-left: 35px; } } }
	&-col-3 {
		@include flex(0 0 100%);
		@include minsm {
			@include flex(0 0 30%); }
		&.right {
			@include minsm {
				padding-left: 35px; } }
		&.col-stucked {
			@include flex(0 0 30%); } }
	&-col-7 {
		@include flex(0 0 100%);
		@include minsm {
			@include flex(0 0 70%); }
		&.left {
			@include minsm {
				padding-right: 35px; } }
		&.col-stucked {
			@include flex(0 0 70%); } }
	&-col-5 {
		@include flex(0 0 100%);
		@include minsm {
			@include flex(0 0 50%); }
		&.right {
			@include minsm {
				padding-left: 35px; } }
		&.left {
			@include minsm {
				padding-right: 35px; } } } }

.m30 {
	margin: 30px auto;
	@include minsm {
		margin: 0; } }
.m30-sm {
	margin: 30px auto; }

.mb25-sm {
	@include minsm {
		margin-bottom: 25px; } }
