// COLORS
$white: #ffffff;
$black: rgba(0, 0, 0, 1);
$white-smoke: rgba(249, 249, 249, 1);
$white-grey-d7: rgba(231, 231, 231, 1);

$red: rgba(253, 16, 10, 1);


// PALLETE
$orange-1: rgba(254, 99, 96,1);
$orange-2: rgba(255, 145, 56,1);
$orange-3: rgba(253, 90, 90,1);
$orange-4: rgba(255, 138, 49,1);
$dark-orange: rgba(255, 132, 0,1);
$night-rider: rgba(52, 52, 52,1);
$zambezi: rgba(98,98,98,1);

$grey: rgba(216,216,216,1);
$grey_dark:	rgba(134, 134, 134, 1);
$graphite: rgba(35,35,35,1);

// THEME
$color-primary: $dark-orange;
$color-accent-1: $red;



$color-background: $white;
$color-background-darken: $white-smoke;
$color-sticker-background: $white;
$color-footer-background: $graphite;

$color-text-invert: $white;
$color-text: $night-rider;
$color-text-weak: $zambezi;
$color-text-strong: $black;

$color-btn-text: $white;
$color-btn-background: $red;
$color-btn-background-hover: $white;
$color-btn-text-hover: $dark-orange;

$color-border: $white-grey-d7;
$color-border-accent: $red;
$color-border-marginator: $grey;

$color-icon: $grey_dark;

// TEXT
$default-font: "Roboto",sans-serif;

// SIZES
$container-max-width-xs: 337px;
$container-max-width-sm: 690px;
$container-max-width-md: 930px;
$container-max-width-xlg: 1345px;
