
.timer-wrapper {
  text-align: center; }

.timer-item {
  display: inline-block;
  vertical-align: middle; }

.timer-val {
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  @include sm {
    padding: 0; } }

.timer-val .text {
  margin: 0;
  font-size: 14px;
  text-align: center;
  line-height: 2.6;
  padding: 5px 0;
  @include sm {
    font-size: 14px; } }

.timer-item .output-wrapper {
  display: block;
  text-align: center;
  width: 90px;
  height: 55px;
  padding: 5px 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  border-radius: 3px;
  background-color: transparent;
  font-size: 35px;
  @include sm {
    padding-top: 8px;
    width: 48px;
    height: 55px;
    line-height: 16px;

    margin: 0 auto; } }

.timer-item .output-wrapper span:first-of-type {
  letter-spacing: 2px; }

.timer-dots {
  color: $black;
  font-size: 28px;
  @include transform(translate(-1px,-0.62rem));
  margin: -3px; }







.button-cta {
  position: relative;
  box-sizing: border-box;
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 17px;
  background-color: $color-btn-background;
  color: $white;
  max-width: 323px;
  width: 100%;
  height: 53px;
  line-height: 53px;
  width: 323px;
  vertical-align: middle;
  @include borad(30px,all);
  &:after {
      position: absolute;
      content: "";
      display:  block;
      right: 12px;
      top: -55px;
      width: 31px;
      height: 53px;
      background: url(../icons/arrow_down_red.svg) no-repeat center center; }
  @include minsm {
    width: 500px;
    max-width: 500px;
    @include borad(38.5px,all);
    font-size: 25px;
    letter-spacing: 1.5px;
    &:after {
      right: -30px;
      top: -65px; }
    line-height: 78px;
    height: 78px; } }

.arrow-wrapper {
  @include center(horizontal);
  top: -22px;
  z-index: 2; }
